@import "../../../../styles/variables";

.portfolio__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: flex-start;
    padding: 150px 0 0 0;
    @include sm-devices {
        padding: 100px 0 0 0;
    }
    .slick-list {
        width: 100%;
        div {
            position: relative;
        }
    }
    .item {
        position: relative;
        display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: transparent;
        border: 2px solid #ffffff;
        min-height: 224px;
    }

    .slider-left-arrow {
        position: absolute;
        top: 50px;
        left: 45%;
        transform: perspective(1500px) rotateY(33deg) translate(-50%, 0);
        cursor: pointer;
        &:hover {
            i {
                transform: scale(1.2);
                transition: .2s ease;
            }
        }
        i {
            font-weight: 900;
            font-size: 60px;
            color: $bg-secondary;
            text-shadow: 1px 1px 2px $color-grey;
            transition: .2s ease;
            transform: none;
        }
        @include sm-devices {
            display: none;
        }
    }

    .slider-right-arrow {
        position: absolute;
        top: 50px;
        right: 45%;
        transform: perspective(1500px) rotateY(-33deg) translate(-50%, 0);
        cursor: pointer;
        &:hover {
            i {
                transform: scale(1.2);
                transition: .2s ease;
            }
        }
        i {
            font-weight: 900;
            font-size: 60px;
            color: $bg-secondary;
            text-shadow: 1px 1px 2px $color-grey;
            transition: .2s ease;
            transform: none;
        }
        @include sm-devices {
            display: none;
        }
    }
}
